<template>
  <div>
    <h1 class="page-title">About</h1>
    <ul>
      <li v-for="item in music">
        <p>{{ item.title }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'about',
    path: '/about',
    data () {
      this.$http.get('/api/v1/post')
        .then(res => (this.music = res.data))
      return { music: [] }
    }
  }
</script>

<style>

</style>



